// Include native bootstrap variables to get upgrade easily
// @import "../../bower_components/bootstrap/less/variables.less";
//
// Variables
// --------------------------------------------------


//== Colors
/////////////////////////////////////////////////////////////
@brand-font-weight-bold: bold;
@brand-font-weight-normal: normal;
// Core color #000 / #FF7900
// Core color #FFF / #F16E00
//@brand-orange: #F16E00; // alternative Orange color for digital
// secondary color #4BB4E6 #FFD200 #50BE87 #A885D8 #FFB4E6
// Greys #f6f6f6 #EEEEEE #DDDDDD #CCCCCC #999999 #666666 #444444 #333333 #232323
//yellow blue green purple pink
@brand-yellow: #FFD200; //black text
@brand-blue: #4BB4E6; //black text
@brand-green: #50BE87;//black text
@brand-purple: #A885D8;//black text
@brand-pink: #FFB4E6;//black text

@brand-orange: #F16E00;
//used to invert colors
@brand-orange-white: #fff;
@brand-orange-black: #000;
@brand-orange-clear: transparent;
/////////////////////////////////////////////////////////////
//
//## Gray and brand colors for use across Bootstrap.

@gray-base:              #000;
@gray-darker:            #333;
@gray-dark:              #666;
@gray:                   #999;
@gray-light:             #ccc;
@gray-light-plus:        #ddd; // used for ghosted states on white BG
@gray-lighter:           #eee;
@gray-lighter-plus:      #f6f6f6;
@gray-lighter-plus-plus: #fafafa;

@gray-footer:            @gray-base;

@brand-primary: @brand-orange;
@brand-success: #32C832;
@brand-info: #527EDB;
@brand-warning: #FFCC00;
@brand-danger: #cd3c14;

//== Scaffolding
//
//## Settings for some of the most global styles.

/// Background color for `<body>`.
@body-bg: @brand-orange-white;
/// Global text color on `<body>`.
@text-color: @gray-base;
/// Global textual link color.
@link-color: @gray-base;
/// Link hover color set via `darken()` function.
@link-hover-color: @brand-primary;
/// Link hover decoration.
@link-hover-decoration: none;


//== Typography
//
//## Font, line-height, and color for body text, headings, and more.
/// orange_sans75_bold for titles
@font-family-titles: "HelvNeueOrange",Helvetica,Arial,sans-serif;
@font-family-sans-serif: "HelvNeueOrange",Helvetica,Arial,sans-serif;
@font-family-serif: "HelvNeueOrange", Helvetica, Georgia, "Times New Roman", Times, serif;
/// Default monospace fonts for `<code>`, `<kbd>`, and `<pre>`.
@font-family-monospace: Menlo, Monaco, Consolas, "Courier New", monospace;
@font-family-base: @font-family-sans-serif;

@html-font-size-base: 62.5%;


@font-size-base: 1.4rem;
@font-size-base-up: 1.6rem;
/// For REM size
@font-size-large: (ceil((@font-size-base * 1.4)*10)/10); // 2rem
@font-size-small: (ceil((@font-size-base * 0.85)*10)/10); // 1.2rem

@font-size-h1: 3.4rem;
@font-size-h2: 3.0rem;
@font-size-h3: 2.4rem;
@font-size-h4: 2rem;
@font-size-h5: 1.8rem;
@font-size-h6: 1.6rem;

@font-size-h1-mobile: 2.0rem;
@font-size-h2-mobile: 1.8rem;
@font-size-h3-mobile: 1.6rem;
@font-size-h4-mobile: 1.4rem;
@font-size-h5-mobile: @font-size-h4-mobile;
@font-size-h6-mobile: @font-size-h5-mobile;

@letter-spacing-h1:-.068rem;
@letter-spacing-h2:-.06rem;
@letter-spacing-h3:-.03rem;
@letter-spacing-h4:-.02rem;
@letter-spacing-h5:-.01rem;

/// Unit-less `line-height` for use in components like buttons.
@line-height-base:        1.428571429; // 20/14
/// Computed "line-height" (`font-size` * `line-height`) for use with `margin`, `padding`, etc.
/// For REM size
@line-height-computed: (floor((@font-size-base * @line-height-base)*10)/10); // 2rem

/// By default, this inherits from the `<body>`.
@headings-font-family: @font-family-titles;
@headings-font-weight: @brand-font-weight-bold;
@headings-line-height: 1.1;
@headings-color: @gray-base;

//== Iconography
//
//## Specify custom location and filename of the included Glyphicons icon font. Useful for those including Bootstrap via Bower.

/// Load fonts from this directory.
@icon-font-path:          "../fonts/";
/// File name for all font files.
@icon-font-name:          "glyphicons-halflings-regular";
/// Element ID within SVG icon file.
@icon-font-svg-id:        "glyphicons_halflingsregular";
/// for REM size
@icon-font-weight:        3rem;


//== Components
//
//## Define common padding and border radius sizes and more. Values based on 14px text and 1.428 line-height (~20px to start).
/// for REM size
@padding-base-vertical: 0.8rem;//8px;
@padding-base-horizontal: 2rem;//20px;

@padding-large-vertical: 1.2rem;//12px;
@padding-large-horizontal: 2rem;//20px;
@padding-small-vertical: 0.5rem;//5px;
@padding-small-horizontal: 1rem;//10px;
@padding-xs-vertical: 0.1rem;//1px;
// Fix by the brand, minimum space = 10px
@padding-xs-horizontal: 1rem;//10px;

@line-height-large:   1.2; // 24/20
@line-height-small:   1.5; // 18/12
@border-radius-base:  0;
@border-radius-large: 0;
@border-radius-small: 0;

/// Global color for active items (e.g., navs or dropdowns).
@component-active-color:    @brand-orange-white;
/// Global background color for active items (e.g., navs or dropdowns).
@component-active-bg:       @gray-base;

/// for REM size
/// Width of the `border` for generating carets that indicator dropdowns.
@caret-width-base:          0.8rem;//8px;
/// Carets increase slightly in size for larger components.
@caret-width-large:         0.8rem;//8px;


//== Tables
//
//## Customizes the `.table` component with basic values, each used across all table variations.

/// Padding for `<th>`s and `<td>`s.
/// for REM size
@table-cell-padding: 0.9rem;//9px;
/// Padding for cells in `.table-condensed`.
@table-condensed-cell-padding:  0.5rem;//5px;

/// Default background color used for all tables.
@table-bg: @brand-orange-white;
/// Background color used for `.table-hover`.
// Occording to the brand, not stripped row
@table-bg-accent: @gray-lighter-plus;
@table-bg-hover:  @gray-lighter;
@table-bg-active: @gray-light-plus;

/// Border color for table and cell borders.
@table-border-color: @gray-light;


//== Buttons
//
//## For each of Bootstrap's buttons, define text, background and border color.
/// for REM size
@btn-font-size: 1.6rem;
@btn-line-height: 1.25; // 20/16
@btn-padding: .7rem 1.8rem .9rem 1.8rem;
@btn-font-size-sm: 1.4rem;
@btn-line-height-sm: @line-height-base; // 20/14
@btn-font-size-lg: 1.6rem;
@btn-line-height-lg: 1.25; // 20/16
@btn-padding-sm : .3rem .8rem .3rem .8rem;
@btn-padding-lg : 1.2rem 2.8rem 1.4rem 2.8rem;

@btn-font-weight: @brand-font-weight-bold;
@btn-default-color: @gray-base;
@btn-default-bg: @brand-orange-white;
@btn-default-border: @btn-default-color;

@btn-primary-color: @brand-orange-white;
@btn-primary-bg: @brand-primary;
@btn-primary-border: @btn-primary-bg;

@btn-success-color: @gray-base;
@btn-success-bg: @brand-success;
@btn-success-border: @btn-success-bg;

@btn-info-color: @brand-orange-white;
@btn-info-bg: @gray-base;
@btn-info-hover-bg: @gray-dark;
@btn-info-active-bg: @brand-orange;
@btn-info-border: @btn-info-bg;

@btn-warning-color: @btn-primary-color;
@btn-warning-bg: @btn-primary-bg;
@btn-warning-border: @btn-primary-border;

@btn-danger-color: @brand-orange-white;
@btn-danger-bg: @brand-danger;
@btn-danger-border: @btn-danger-bg;

@btn-primary-color: @brand-orange-white;
@btn-primary-bg: @brand-primary;
@btn-primary-hover-color: @btn-primary-color;
@btn-primary-hover-bg: @brand-orange-black;
@btn-primary-active-color: @btn-primary-color;
@btn-primary-active-bg: @btn-primary-bg;

@btn-secondary-color: @brand-orange-black;
@btn-secondary-bg: @brand-orange-clear;

@btn-success-color: @brand-orange-black;
@btn-success-bg: @brand-success;

@btn-info-color: @brand-orange-white;
@btn-info-bg: @brand-orange-black;
@btn-info-hover-color: @brand-orange-black;
@btn-info-hover-bg: @brand-orange-white;

@btn-link-disabled-color: @gray-light;
@btn-link-disabled-black-bg-color: @gray-dark;
@link-color-disabled: @gray-light;
@link-hover-decoration: none;
@link-hover-color-disabled: @link-color-disabled;
@link-color-inverse: @brand-orange-white;
@link-hover-color-inverse: @brand-orange;
@link-color-inverse-disabled: @gray-dark;
@link-hover-color-inverse-disabled: @gray-dark;

// Allows for customizing button radius independently from global border radius
@btn-border-radius-base:         @border-radius-base;
@btn-border-radius-large:        @border-radius-large;
@btn-border-radius-small:        @border-radius-small;

//== Forms
//
//##

/// `<input>` background color
@input-bg: @brand-orange-white;
/// `<input disabled>` background color
@input-bg-disabled: @gray-lighter;

/// Text color for `<input>`s
@input-color: @gray-base;
/// `<input>` border color
@input-border: @gray-light;

// TODO: Rename `@input-border-radius` to `@input-border-radius-base` in v4
/// Default `.form-control` border radius
// This has no effect on `<select>`s in some browsers, due to the limited stylability of `<select>`s in CSS.
@input-border-radius:            @border-radius-base;
/// Large `.form-control` border radius
@input-border-radius-large:      @border-radius-large;
/// Small `.form-control` border radius
@input-border-radius-small:      @border-radius-small;

/// Border color for inputs on focus
@input-border-focus: @gray;

/// Placeholder text color
@input-color-placeholder: @gray-dark;

/// Default `.form-control` height
// for REM size
@input-height-base:              (2.4 + (@padding-base-vertical * 2) );
/// Large `.form-control` height
@input-height-large:             ((ceil(@font-size-large * @line-height-large * 10) + (@padding-large-vertical * 20) + 4) / 10);
/// Small `.form-control` height
@input-height-small:             ((floor(@font-size-small * @line-height-small * 10) + (@padding-small-vertical * 20) + 4) / 10);

@input-padding-x-sm: .8rem;
@input-padding-y-sm: .425rem;
@input-padding-x-lg: 2.4rem;
@input-padding-y-lg: 1.175rem;

@input-select-padding-y: .7rem;
@input-select-padding-y-sm: .3rem;
@input-select-padding-y-lg: 1.05rem;

/// `.form-group` margin
@form-group-margin-bottom:       2rem;
@form-search-margin-right:       .6rem;

@legend-color:                   @gray-darker;
@legend-border-color:            @gray-lighter;

/// Background color for textual input addons
@input-group-addon-bg:           @brand-orange-white;
/// Border color for textual input addons
@input-group-addon-border-color: @gray-base;

// checkbox-label toggle padding
@checkbox-toggle-padding-vertical:  1rem;
@checkbox-label-width:              6rem;
@checkbox-label-height:             3rem;
@checkbox-label-size:               2.6rem;

// 3-way switch
@toggles-3-way-width:               4rem;

/// Disabled cursor for form controls and buttons.
@cursor-disabled:                not-allowed;


//== Dropdowns
//
//## Dropdown menu container and contents.

/// Background for the dropdown menu.
@dropdown-bg:                    @brand-orange-white;
/// Dropdown menu `border-color`.
@dropdown-border:                rgba(0,0,0,.15);
/// Dropdown menu `border-color` for IE8.
@dropdown-fallback-border:       @gray-base;

/// Divider color for between dropdown items.
@dropdown-divider-bg:            @gray-lighter;

/// Dropdown link text color.
@dropdown-link-color:            @gray-base;
/// Hover color for dropdown links.
@dropdown-link-hover-color:      @brand-orange-white;
/// Hover background for dropdown links.
@dropdown-link-hover-bg:         @gray-base;

/// Active dropdown menu item text color.
@dropdown-link-active-color:     @brand-orange-white;
/// Active dropdown menu item background color.
@dropdown-link-active-bg:        @gray-base;

/// Disabled dropdown menu item background color.
@dropdown-link-disabled-color:   @gray-dark;

/// Text color for headers within dropdown menus.
@dropdown-header-color:          @gray-base;

/// Deprecated `@dropdown-caret-color` as of v3.1.0
@dropdown-caret-color:           @gray-base;


//-- Z-index master list
//
// Warning: Avoid customizing these values. They're used for a bird's eye view
// of components dependent on the z-axis and are designed to all work together.
//
// Note: These variables are not generated into the Customizer.

@zindex-navbar:            1000;
@zindex-dropdown:          1000;
@zindex-popover:           1060;
@zindex-tooltip:           1070;
@zindex-navbar-fixed:      1030;
@zindex-modal-background:  1040;
@zindex-modal:             1050;


//== Media queries breakpoints
//
//## Define the breakpoints at which your layout will change, adapting to different screen sizes.
// Small screen / tablet
@screen-sm-min:              480px;
// Backward navbar.less compatibility deprecated till v3.2.0
@screen-xs-min:              @screen-sm-min;

// Medium screen / desktop
@screen-md-min:              768px;

// Large screen / wide desktop
@screen-lg-min:              980px;

// Large screen / wide desktop
@screen-xl-min:              1220px;

// Large screen / wide desktop
@screen-xxl-min:              1380px;

// So media queries don't overlap when required, provide a maximum
@screen-xs-max:              (@screen-sm-min - 1);
@screen-sm-max:              (@screen-md-min - 1);
@screen-md-max:              (@screen-lg-min - 1);
@screen-lg-max:              (@screen-xl-min - 1);
@screen-xl-max:              (@screen-xxl-min - 1);


//== Grid system
//
//## Define your custom responsive grid.

/// Number of columns in the grid.
@grid-columns:              12;
/// Padding between columns. Gets divided in half for the left and right.
/// For REM size
@grid-gutter-width:         20px;

@grid-gutter-xs-width:      10px;
@grid-gutter-sm-width:      10px;
@grid-gutter-md-width:      20px;
@grid-gutter-lg-width:      20px;
@grid-gutter-xl-width:      20px;
@grid-gutter-xxl-width:     20px;
// Navbar collapse
/// Point at which the navbar becomes uncollapsed.
@grid-float-breakpoint:     @screen-md-min;
/// Point at which the navbar begins collapsing.
@grid-float-breakpoint-max: (@grid-float-breakpoint - 1);

// o-container-fluid security margins acording to the brand
@o-container-fluid-xs-margin:      1.25%;
@o-container-fluid-sm-margin:      1.25%;
@o-container-fluid-md-margin:      1.5625%;
@o-container-fluid-lg-margin:      3.125%;
@o-container-fluid-xl-margin:      3.125%;
@o-container-fluid-xxl-margin:     4.16667%;


//== Container sizes
//
//## Define the maximum width of `.container` for different screen sizes.
@container-xs: 312px;
@container-sm: 468px;
@container-md: 744px;
@container-lg: 960px;
@container-xl: 1200px;
@container-xxl: 1320px;


//== Navbar
