/// For REM size
@navbar-padding-vertical:          1rem;//((@navbar-height - @line-height-computed) / 2); //1.5 rem
@navbar-collapse-max-height:       340px;

@navbar-item-margin-top: 4.8rem;
@navbar-brand-logo-padding: 2.5rem;
@navbar-brand-logo-height: 5rem;
@navbar-brand-logo-focus-offset: 1rem;
@navbar-item-margin-top-minimized: 3.3rem;
@navbar-brand-logo-padding-minimized: 2rem;
@navbar-brand-logo-height-minimized: 4rem;


