@panel-inner-border:          @gray-base;
@panel-footer-bg:             @brand-orange-white;

@panel-default-text:          @gray-base;
@panel-default-border:        @gray-light;
@panel-default-heading-bg:    @brand-orange-white;

@panel-primary-text:          @gray-base;
@panel-primary-border:        @brand-primary;
@panel-primary-heading-bg:    @brand-primary;

@panel-success-text:          @gray-base;
@panel-success-border:        @state-success-border;
@panel-success-heading-bg:    @brand-success;

@panel-info-text: @gray-base;
@panel-info-border: @brand-info;
@panel-info-heading-bg: @brand-info;

@panel-warning-text: @gray-base;
@panel-warning-border: @brand-warning;
@panel-warning-heading-bg: @brand-warning;

@panel-danger-text: @brand-orange-white;
@panel-danger-border: @brand-danger;
@panel-danger-heading-bg: @brand-danger;

@o-panel-height: 19.5rem;
@o-panel-block-padding: 1rem 1.2rem;
@o-panel-block-min-height: 7rem;

@o-panel-height-sm: 21.5rem;
@o-panel-height-xxl: 24rem;
