@o-panel-height-md: 14.2rem;
@o-panel-block-min-height-md: 6rem;


//== Thumbnails
//
//##

/// Padding around the thumbnail image
@thumbnail-padding:           0;
/// Thumbnail background color
@thumbnail-bg:                @gray-base;
/// Thumbnail border color
@thumbnail-border:            @gray-base;
/// Thumbnail border radius
@thumbnail-border-radius:     @border-radius-base;

/// Custom text color for thumbnail captions
@thumbnail-caption-color:     @brand-orange-white;
/// Padding around the thumbnail caption
/// for REM size
@thumbnail-caption-padding:   2rem;


//== Wells
//
//##

@well-bg:                     @brand-orange-white;
@well-border:                 @gray-base;


//== Badges
//
//##

@badge-color:                 @gray-base;
/// Linked badge text color on hover
@badge-link-hover-color:      @brand-orange;
@badge-bg:                    @brand-orange-white;

/// Badge text color in active nav link
@badge-active-color:          @brand-orange-white;
/// Badge background color in active nav link
@badge-active-bg:             @gray-base;

@badge-font-weight:           @brand-font-weight-bold;
@badge-line-height:           1;
@badge-border-radius:         0;


//== Breadcrumbs
//
//##

@breadcrumb-padding-vertical:   10px;
@breadcrumb-padding-horizontal: 15px;
@breadcrumb-separator-padding-vertical: 0;
@breadcrumb-separator-padding-horizontal: 10px;
@breadcrumb-separator-size: 1rem;
/// Breadcrumb background color
@breadcrumb-bg:                 @brand-orange-white;
/// Breadcrumb text color
@breadcrumb-color:              @gray-base;
/// Text color of current page in the breadcrumb
@breadcrumb-active-color:       @brand-primary;
/// Textual separator for between breadcrumb elements
@breadcrumb-separator: ">";


//== o-link-arrow
//
//##

@o-link-arrow-separator-padding-vertical: 0;
@o-link-arrow-separator-padding-horizontal: 10px;
@o-link-arrow-color: @gray-base;
@o-link-arrow-separator-size: 1rem;

//== Carousel
//
//##

@carousel-text-shadow:                        0 1px 2px rgba(0,0,0,.6);

@carousel-control-color:                      @gray-base;
@carousel-control-width:                      3rem;
@carousel-control-opacity:                    .5;
@carousel-control-font-size:                  @font-size-large;

@carousel-indicator-active-bg:                @brand-primary;
@carousel-indicator-border-color:             @gray-base;
@carousel-indicator-height:                   .4rem;

@carousel-caption-color:                      @gray-base;
@carousel-margin-bottom:                      3rem;


//== Close
//
//##

@close-font-weight:           @brand-font-weight-bold;
@close-color:                 @gray-base;
@close-text-shadow:           0 1px 0 @brand-orange-white;


//== Code
//
//##

@code-color: @gray-darker;
@code-bg: @gray-lighter;
@kbd-color: @gray-base;
@kbd-bg: @gray-lighter;
@pre-bg: @gray-lighter-plus;
@pre-color: @gray-darker;
@pre-border-color: @gray-light;
@pre-scrollable-max-height: 340px;



//== Type
//
//##

/// Horizontal offset for forms and lists.
@component-offset-horizontal: 180px;
/// Text muted color
@text-muted:                  @gray-dark;
/// Abbreviations and acronyms border color
@abbr-border-color:           @gray-dark;
/// Headings small color
@headings-small-color:        @gray-dark;
/// Blockquote small color
@blockquote-small-color:      @gray-dark;
/// Blockquote font size
@blockquote-font-size:        (@font-size-base * 1.25);
/// Blockquote border color
@blockquote-border-color:     @gray-lighter;
/// Page header border color
@page-header-border-color:    @gray-lighter;
